<template>
  <div class="welcome">
    {{msg}}
    <el-button @click="visible = true">按钮</el-button>
    <el-dialog :visible.sync="visible" title="Hello world">
      <p>欢迎使用 Element</p>
    </el-dialog>
    <router-link to="/">首页</router-link>
  </div>
</template>
<script>
export default {
  name: 'Welcome',
  data () {
    return {
      msg: 'this is welcome index page',
      visible: false
    }
  }
}
</script>


